<template>
    <v-list dense>
        <template v-for="item in pages">
          <v-layout v-if="item.heading" :key="item.heading" row align-center>
            <v-flex xs6>
              <v-subheader v-if="item.heading">
                {{ item.heading }}
              </v-subheader>
            </v-flex>
            <v-flex xs6 class="text-xs-center">
              <a href="#!" class="body-2 black--text">EDIT</a>
            </v-flex>
          </v-layout>
          <v-list-group v-else-if="item.children" v-model="item.model" :key="item.text" :prepend-icon="item.model ? item.icon : item['icon-alt']" append-icon="">
            <v-list-tile slot="activator">
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ item.text }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile v-for="(child, i) in item.children" :key="i" @click="choisePage(child.link)">
              <v-list-tile-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ child.text }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
          <v-list-tile v-else :key="item.text" @click="choisePage(item.link)">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                {{ item.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>
    </v-list>
</template>

<script>

export default {
  name: 'DrawerLeft',
  data(){
    return{
      pages: [
        { icon: 'movie', text: '', link: '/movies' },
        { icon: 'tv', text: '', link: '/series' },
        { icon: 'people', text: '', link: '/actors' },
        { icon: 'keyboard_arrow_up', 'icon-alt': 'keyboard_arrow_down', text: ' ', model: false,
          children: [
            { text: '', link: '/movies/upcoming' },
            { text: '  ', link: '/movies/nowPlaying' },
            { text: '', link: '/movies/Popular' },
            { text: ' 100', link: '/movies/topRated' }
          ]
        },
        { icon: 'keyboard_arrow_up', 'icon-alt': 'keyboard_arrow_down', text: ' ', model: false,
          children: [
            { text: '', link: '/series/popular' },
            { text: '  ', link: '/series/onTheAir' },
            { text: ' 100', link: '/series/topRated' }
          ]
        },
        { icon: 'bookmark', text: '', link: '/bookmarks' },
        { icon: 'history', text: ' ', link: '/viewed' },
        { icon: 'search', text: ' ', link: '/' },
        { icon: 'account_circle', text: '  ', link: '/' },
      ]
    }
  },
  methods: {
    choisePage(link){
      this.$router.push(link)
      this.pages.forEach(item => {item.model ? item.model = false : null})
    }
  }
}
</script>

<style lang="scss" scoped>

</style>


